.cartItem {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: scroll;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  color: orange;
  margin: 1rem;
}

.actions {
  text-align: center;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #892b06;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 4rem;
}

.actions button:hover {
  background-color: #501a01;
  border-color: #501a01;
  color: white;
}

.actions button {
  background-color: #892b06;
  color: whiter;
}


















