.button {
  cursor: pointer;
  font: inherit;
  border: none;
  width: 15rem;
  background-color: #4d1601;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
  margin-right: 150px;
  font-size: 22px;
  height: 45px;
}

.button:hover,
.button:active {
    background-color: #2c0d00;
}


.icon img {
    width: 42px;
    height: 42px;
    margin-right: 15px;
    color: white;
}

.badge {
    background-color: #b94517;
    padding: 0.25rem 1rem;
    border-radius: 25px;
    margin-left: 1rem;
    font-weight: bold;
}
